import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import Moment from "react-moment";
import "moment-timezone";

import notification from "../../../../components/notification";
import { Skeleton, Divider } from "antd";
import NumberFormat from "../../../App/NumberFormat";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import locationActions from "../../../../redux/companyMaster/location/actions";
import { Box, StatusTag } from "../../../App/app.style";

class ShowLocation extends Component {
  // Get All data diawal
  componentDidMount() {
    const { showLocation } = this.props;
    const { id } = this.props.match.params;
    showLocation(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.location.error) {
      notification("error", this.props.location.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.location.success) {
      notification("success", this.props.location.success);
      this.props.clearSuccess();
    }
  }

  render() {
    // console.log(this.props);
    const redirectTo = "/dashboard/companies/locations";

    // Data setup
    const { location } = this.props.location;
    const account = location ? location.account_item_transfer : null;
    const site = location ? location.site : null;

    // console.log(this.props);
    return (
      <LayoutWrapper>
        <PageHeader>
          <IntlMessages id="locations.pageHeader" />
        </PageHeader>
        <Box>
          {this.props.location.loading ? (
            <Skeleton paragraph={{ rows: 9 }} active />
          ) : (
            <div>
              <div className="isoInvoiceTableBtn">
                <h3 className="isoCode">
                  <IntlMessages id="locations.show.code" />{" "}
                  {location ? location.location_code : ""}
                </h3>
                <Button
                  onClick={e => {
                    e.preventDefault();
                    this.props.history.push(redirectTo);
                  }}
                >
                  <IntlMessages id="button.back" />
                </Button>
              </div>
              <div className="isoDetailDataFlex">
                <div className="isoColumn-1">
                  <p>
                    <IntlMessages id="locations.show.name" />{" "}
                    {location ? location.location_name : ""}
                  </p>
                  <p>
                    <IntlMessages id="locations.show.leadTime" />{" "}
                    {location ? location.lead_time : ""}{" "}
                    <IntlMessages id="locations.show.leadTime.unit" />
                  </p>
                </div>
                <div className="isoColumn-2">
                  <p>
                    <IntlMessages id="locations.show.inventoryType" />{" "}
                    {location
                      ? location.inventory_type === 1
                        ? "Normal"
                        : "Minus"
                      : ""}
                  </p>
                  <p>
                    <IntlMessages id="locations.show.costingMethod" />{" "}
                    {location
                      ? location.costing_method === 1
                        ? "Average"
                        : "FIFO"
                      : ""}
                  </p>
                </div>
                <div className="isoColumn-2" style={{ display: "flex" }}>
                  <span style={{ marginLeft: "auto" }}>
                    <IntlMessages id="locations.show.status" />{" "}
                    {location ? (
                      location.is_active ? (
                        <StatusTag className="active">Active</StatusTag>
                      ) : (
                        <StatusTag className="nonactive">Inactive</StatusTag>
                      )
                    ) : (
                      ""
                    )}
                  </span>
                </div>
              </div>
              <div className="isoDetailDataBlock">
                <div className="isoColumn-1">
                  <p>
                    <IntlMessages id="locations.show.description" />{" "}
                    {location ? location.description : ""}
                  </p>
                </div>
              </div>
              <Divider />
              <div className="isoDetailDataFlex">
                <div className="isoColumn-2">
                  <p>
                    <IntlMessages id="locations.show.phone" />{" "}
                    {location ? location.phone : ""}
                  </p>
                  <p>
                    <IntlMessages id="locations.show.email" />{" "}
                    {location ? location.email : ""}
                  </p>
                </div>
                <div className="isoColumn-2">
                  <p>
                    <IntlMessages id="locations.show.site.code" />{" "}
                    {site ? site.site_code : ""}
                  </p>
                  <p>
                    <IntlMessages id="locations.show.site.name" />{" "}
                    {site ? site.site_name : ""}
                  </p>
                </div>
              </div>
              <div className="isoDetailDataBlock">
                <p>
                  <IntlMessages id="locations.show.address" />{" "}
                  {location ? location.address : ""}
                </p>
                <p>
                  <IntlMessages id="locations.show.url" />{" "}
                  <a href={location ? location.url : ""}>
                    {location ? location.url : ""}
                  </a>
                </p>
              </div>
            </div>
          )}
        </Box>
        <Box>
          {this.props.location.loading ? (
            <Skeleton paragraph={{ rows: 5 }} active />
          ) : (
            <div>
              <div className="isoInvoiceTableBtn">
                <h3 className="isoCode">
                  <IntlMessages id="locations.show.account" />
                </h3>
              </div>
              <div className="isoDetailDataFlex">
                <div className="isoColumn-2">
                  <p>
                    <IntlMessages id="locations.show.name" />{" "}
                    {account
                      ? account.account_code + " " + account.account_name
                      : ""}
                  </p>
                </div>
                <div className="isoColumn-2">
                  <p>
                    <IntlMessages id="locations.show.status" />{" "}
                    {account ? (
                      account.is_active ? (
                        <StatusTag className="active">Active</StatusTag>
                      ) : (
                        <StatusTag className="nonactive">Inactive</StatusTag>
                      )
                    ) : (
                      ""
                    )}
                  </p>
                </div>
              </div>
              <div className="isoDetailDataBlock">
                <div className="isoColumn-1">
                  <p>
                    <IntlMessages id="locations.show.description" />{" "}
                    {account ? account.description : ""}
                  </p>
                </div>
              </div>
              <Divider />
              <div className="isoDetailDataFlex">
                <div className="isoColumn-2">
                  <IntlMessages id="locations.show.account.openingBalance" />{" "}
                  {account ? (
                    <NumberFormat
                      value={account.opening_balance}
                      displayType={"text"}
                      thousandSeparator={true}
                      prefix={account.currency.currency_code + " "}
                    />
                  ) : (
                    ""
                  )}
                </div>
                <div className="isoColumn-2">
                  <IntlMessages id="locations.show.account.openingBalanceDate" />{" "}
                  {location ? (
                    <Moment
                      parse="YYYY-MM-DD HH:mm"
                      format="DD MMMM YYYY, HH:mm"
                    >
                      {account.opening_balance_date}
                    </Moment>
                  ) : (
                    ""
                  )}
                </div>
              </div>
            </div>
          )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { location: state.location };
};

export default compose(
  connect(
    mapStateToProps,
    locationActions
  )
)(ShowLocation);
