import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import NumberFormat from "react-number-format";


class GlobalNumberFormat extends Component {

  render() {
    const { user } = this.props.auth;
    const { prefix, suffix } = this.props;
    let { number } = user ? user : [];

    return (
      <NumberFormat
        value={parseFloat(parseFloat(this.props.value).toFixed(6))}
        displayType={'text'}
        thousandSeparator={number ? number.separator : ','}
        decimalSeparator={number ? number.decimal : '.'}
        decimalScale={number ? parseInt(number.precision) : 2}
        fixedDecimalScale={number ? parseInt(number.fixed_precision) === 1 ? true : false : false}
        prefix={prefix ? prefix : undefined}
        suffix={suffix ? suffix : undefined}
      />

    )
  }
}



const mapStateToProps = state => {
  return {
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
  )
)(GlobalNumberFormat);
